.about-us h2 {
  margin: 0;
  margin-bottom: 50px;
  padding-top: 150px;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  color: $simple-black;
  text-align: center;

  @media (max-width: 1023px) {
    margin-bottom: 30px;
    padding-top: 70px;
    font-size: 24px;
    line-height: 41px;
  }
}

.about-us__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1205px;
  margin: 0 auto;
  padding: 0;
  padding-bottom: 98px;
  counter-reset: list-counter;
  list-style: none;

  @media (max-width: 1023px) {
    max-width: 590px;
    padding-bottom: 32px;
  }

  @media (max-width: 767px) {
    padding-bottom: 50px;
  }
}

.about-us__item {
  position: relative;
  width: 263px;
  counter-increment: list-counter;
  padding: 54px 19px 50px;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
  letter-spacing: -0.5px;

  @media (max-width: 1023px) {
    padding: 54px 15px 42px;
  }

  @media (max-width: 767px) {
    font-size: 18px;
    padding-bottom: 36px;
  }

  &::before {
    position: absolute;
    width: 263px;
    height: 60px;
    left: 50%;
    transform: translateX(-50%);
    top: 0;

    content: "0" counter(list-counter);

    font-weight: bold;
    font-size: 28px;
    line-height: 38px;
    text-align: center;
    color: $turquoise;

    @media (max-width: 767px) {
      top: 4px;
    }
  }
}
